<template>
	<section>
		 <div class="zx-banner">
      <img src="../../../static/images/zx_banner2.jpg" alt="">
      <ul class="zx-zl-dzsj">
        <li>
          <span>{{info.articleCount||0}}篇</span>
          <em>文章</em>
        </li>
        <li>
          <span>{{info.browseCount||0}}</span>
          <em>总阅读</em>
        </li>
        <li>&nbsp;</li>
        <li>
          <span>{{info.fabulousCount||0}}</span>
          <em>被赞</em>
        </li>
        <li>
          <span>{{info.subscribeCount||0}}</span>
          <em>订阅量</em>
        </li>
      </ul>
      <div class="zx-myho zx-zlbn">
        <span><img :src="info.columnCover" alt=""></span>
        <em>{{info.columnTitle}}<a href="javascript:;" class="zx-my-tgz"  v-model="info.subStatus" @click="subscription(info)">{{info.subStatus==1?'已订阅':'订阅专栏'}}</a></em>
      </div>
    </div>
    
    <div class="inner">
      <div class="zx-zlyl-t">
				<h1>{{info.authorName}}</h1>
        <h2 style="font-size: 16px;">{{info.columnBrief}}</h2>
        <p  v-html="info.columnBriefHtml"></p>
      </div>
      <ul class="zx-myhr-zlwz clearfix">
          <li class="clearfix" v-for="item in list">
            <div class="zx-myhr-zz-l"><img :src="item.extend1" alt=""></div>
            <div class="zx-myhr-zz-r">
              <h2 @click="getArticle(item.uuid)"><a href="javascript:;" :title="item.title">{{item.title|truncate(40)}}</a></h2>
              <p style="height:50px;overflow: hidden;">{{item.tex|truncate(121)}}</p>
              <div>
							
										<em>{{item.releaseDate | timestampFormat('YYYY-MM-DD') }}</em> 
	                  

               <span>
                  <i class="zx-icon zx-ico-yl"></i>{{item.browseCount}}
                  <i class="zx-icon zx-ico-sc1"></i>{{item.collectionCount}}
                  <i class="zx-icon zx-ico-dz"></i>{{item.fabulousCount}}
                </span>
              </div>
            </div>
          </li>
        </ul>
        <div class="c-page zx-page1">
            <el-pagination
            	:current-page.sync ='currentpage'
       			:page-size="pageSize"
		      	@current-change="pageFn"
			  	layout="prev, pager, next"
			  	:total=total>
			</el-pagination>
	   </div>
    </div>

	</section>
</template>

<script>
	import {columnDetails,columnArticle,dingYue,cancle} from '../../api/info';
	export default{
		data(){
			return{
				id:'',
				info:{},
				list:[],
				total:0,
				currentpage:1,
				pageSize:3
			}
		},
		methods:{
			subscription(item){
				var param={
					columnId:item.id
				}
				if(item.subStatus==0){
					item.subStatus=1
					dingYue(param).then(res=>{
					
					})
				}else if(item.subStatus==1){
					item.subStatus=0
					cancle(param).then(res=>{
					
					})
				}	
			},
			getArticle(uuid){
				this.$router.push({path:'/ZLactive',query:{uuid:uuid}})
			},
			pageFn(str){
				var param={
					pageSize:3,
					pageNum:str,
					id:this.id
				}
				columnArticle(param).then(res=>{
					
					this.list=res.datas;
					this.total=res.total;
				})
			},
			columnDetailsFn(){
				var param={
					id:this.id
				}
				columnDetails(param).then(res=>{
					console.log(res)
					this.info=res.datas
				})
			},
			columnArticleFn(){
				var param={
					id:this.id
				}
				columnArticle(param).then(res=>{
					
					this.list=res.datas;
					this.total=res.total;
				})
			},
			getId(){
				this.id= this.$route.query.id
			}
		},
		mounted(){
			this.getId();
			this.columnDetailsFn();
			this.columnArticleFn();
		}
	}
</script>

<style>
</style>


